<template>
  <div class="badge" :style="{backgroundColor:bgColor,color:color}">
    {{text}}
  </div>
</template>

<script>
  export default {
    name: "Badge",
    props:{
      text:String,
      bgColor:{
        type:String,
        default:'#F46670'
      },
      color:{
        type:String,
        default: '#ffffff'
      }
    },
  }
</script>

<style scoped lang="less">
  .badge{
    margin: 2px 8px 2px 0;
    padding: 2px 10px;
    border-radius: 15px;
    font-size: 14px;
    cursor: pointer;
    @media screen{
      @media (max-width: 1200px){
        padding: 0 7px;
        font-size: 12px;
      }
    }
  }
</style>