<template>
  <div id="detail">
    <scroll class="wrapper" ref="scroll" :probe-type="3" :listen-scroll="listenScroll"
            :pullup="true" :pulldown="true"
            @scroll="ContentScroll" @pullingUp="pullUp" @pullingDown="pullDown">
      <Row type="flex" justify="center" align="top" style="padding-top: 20px;padding-bottom: 20px">
        <Col :xs="{span:24}" :sm="{span:23}" :lg="{span:18}" :xl="{span:15}">
          <div class="crumb">
            <Breadcrumb>
              <BreadcrumbItem to="/" >
                <i class="fas fa-home"></i> 首页
              </BreadcrumbItem>
              <BreadcrumbItem to="/blogs">
                <i class="fas fa-blog"></i> 博客
              </BreadcrumbItem>
              <BreadcrumbItem>
                <i class="fas fa-location-arrow"></i> 详情
              </BreadcrumbItem>
            </Breadcrumb>
          </div>
          <div class="banner">
            <span class="title">
              {{article.title}}
            </span>
            <div class="info">
              <Badge v-for="tag in article.tags" :key="tag" :text="tag"></Badge>
              <span>
                <i class="fas fa-heart"></i>{{article.supports}}
              </span>
              <span>
                <i class="fas fa-eye"></i>{{article.total_views}}
              </span>
              <span>
                <i class="fas fa-clock"></i>{{article.created}}</span>
            </div>
          </div>

          <div class="body">
            <span v-html="article.body" v-highlight></span>
          </div>

          <div class="mobile-area">
            <button class="fill-btn pink buzz">
              <span>点个赞吧</span>
              <i class="fas fa-thumbs-up"></i>
            </button>

            <button class="fill-btn doulv">
              <span>分享一下</span>
              <i class="fas fa-share"></i>
            </button>
          </div>

          <div class="footer">
            <div class="left">
              <button class="skip-btn to-right" @click="toPrevious">
                <i class="fas fa-arrow-left"></i>
                <span>上一篇</span>
              </button>
            </div>

            <div class="center">
              <button class="fill-btn pink buzz">
                <span>点个赞吧</span>
                <i class="fas fa-thumbs-up"></i>
              </button>

              <button class="fill-btn doulv">
                <span>分享一下</span>
                <i class="fas fa-share"></i>
              </button>
            </div>

            <div class="right">
              <button class="skip-btn to-left" @click="toNext">
                <span>下一篇</span>
                <i class="fas fa-arrow-right"></i>
              </button>
            </div>
          </div>
        </Col>

        <Col :xs="{span:0}" :sm="{span:0}" :lg="{span:0}" :xl="{span:4}">
          <div class="blog-toc" id="blog-toc" :style="fixedStyle">
            <div class="title">
              <i class="fas fa-list-ul"></i>
              <span>目录</span>
            </div>
            <span style="width: 100%" v-html="toc"></span>
          </div>
        </Col>
      </Row>
    </scroll>
    <back-top @click.native="backTop" v-show="isBackTopShow"></back-top>
  </div>
</template>

<script>
  import Badge from "components/common/widgets/Badge";
  import Scroll from "components/common/Scroll/Scroll";
  import BackTop from "components/common/backtop/BackTop";

  import {getArticlebyId} from "network/detail";

  export default {
    name: "Detail",
    components:{
      Badge,
      Scroll,
      BackTop
    },
    data(){
      return{
        article: {},
        toc: null,
        device_width:document.body.clientWidth || document.documentElement.clientWidth,
        listenScroll:true, //是否监听scroll
        fixedStyle: {},
        isBackTopShow:false, //是否展示backtop组件
        blogId:0
      }
    },
    created() {
      this.blogId = this.$route.params['blogId'];
      // 请求获取博客数据
      this.getArticlebyId();
      // 获取博客数据后scroll重新计算
      this.$nextTick(function () {
        console.log('computed');
        this.getArticlebyId();
      })
    },
    mounted() {
      //挂载scroll监听事件
      window.addEventListener('scroll', this.handleScroll, true);
      //监听设备变化
      this.$bus.$on('browserChange',(val) => {
        this.device_width = val;
      })
    },
    watch: {
      $route(to, from) {
        this.$router.go(0);
      },
      article(){
        this.$refs.scroll.refresh();
      }
    },
    methods:{
      //网络请求的封装
      getArticlebyId(){
        getArticlebyId(this.blogId).then(res => {
          this.$set(this.$data,'article',res.article)
          this.toc = res.toc;
        })
      },
      toPrevious(){
        this.$router.push({
          name:'blogDetail',
          params:{
            blogId:(this.article.id - 1)
          }
        })
        /* route跳转时，如果是同类型的路由，比如 从blog/1 跳转到 blog/2，则原先的组件实例会被复用，
          此时会出现页面不刷新的状况，此时可以监听路由的变化作出正确响应 */
      },
      toNext(){
        this.$router.push({
          name:'blogDetail',
          params:{
            blogId:(this.article.id + 1)
          }
        })
      },

      //
      ContentScroll(position){
        this.isBackTopShow = (-position.y) > 600;
      },
      handleScroll(){
        /* 监听pc端滚动事件，如果高度大于20使右侧侧边栏黏性固定 */
        let scrollHeight = document.documentElement.scrollTop || document.body.scrollTop;
        if(scrollHeight > 20){
          let widget = document.querySelector('#blog-toc');
          let widget_width = widget.offsetWidth.toString();
          this.$set(this.fixedStyle,'position','fixed');
          this.$set(this.fixedStyle,'width',widget_width + 'px');
        }
        this.isBackTopShow = scrollHeight > 500;
      },
      pullUp(){
        console.log('pullup');
        this.$refs.scroll.finishPullUp();
      },
      pullDown(){
        console.log('pulldown');
        this.$refs.scroll.finishPullDown();
      },

      /* 回到顶部 */
      backTop(){
        if(this.device_width < 1200){
          this.$refs.scroll.scrollTo(0,0,1000);
        }else {
          window.scrollTo({
            top: 0,
            behavior: "smooth"
          });
          //document.body.scrollTop=document.documentElement.scrollTop=0;
        }
      },
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll);   //  离开页面清除（移除）滚轮滚动事件
    }
  }
</script>

<style scoped lang="less">
  @keyframes icon-rotate {
    0%{
      transform: rotate(0deg);
    }
    100%{
      transform: rotate(360deg);
    }
  }
  #detail{
    background: url(../../assets/img/bg3.gif) repeat;
    height: auto;
    position: relative;

    @media screen{
      @media (max-width: 568px){
        height: calc(100vh - 44px);
      }
      @media (min-width: 569px) and (max-width: 992px){
        height: calc(100vh - 56px);
      }
      @media (min-width: 993px) and (max-width: 1200px){
        height: calc(100vh - 56px);
      }

    }

    .wrapper{
      height: auto;
      @media screen{
        @media (max-width: 568px){
          position: absolute;
          top: 0;bottom: 0;
          left: 5px;right: 5px;
        }
        @media (min-width: 569px) and (max-width: 1200px){
          position: absolute;
          top: 0;bottom: 0;
          left: 0;right: 0;
        }
      }
    }

    .banner,.body,.footer,.mobile-area{
      margin-bottom: 15px;
      background: @color-white;
      border-radius: 10px;
      width: 100%;
    }

    .crumb{
      margin-bottom: 10px;
      width: 100%;
      padding: 5px;
      @media screen{
        @media (max-width: 768px){
          padding: 2px;
        }
      }
    }

    .banner{
      padding: 10px 25px;
      display: flex;
      flex-direction: column;
      align-items: start;
      .title{
        font-size: 24px;
        font-weight: 600;
        letter-spacing: 2px;
      }
      .info{
        span{
          margin-left: 10px;
          font-weight: bold;
          font-size: 14px;
          color: grey;
          i{
            margin-right: 3px;
          }
        }
        display: flex;
        align-items: center;
      }
      @media screen{
        @media (max-width: 1200px){
          padding: 5px 10px;
          .title{
            font-size: 18px;
          }
          .info{
            span{
              font-size: 12px;
            }
          }
        }
      }
    }

    .body{
      overflow: hidden;
      padding: 15px 30px;
      background: @color-white;
      color: gray;
      border-radius: 10px;
      font-size: 16px;

      @media screen{
        @media(max-width: 768px){
          padding: 10px 8px;
          font-size: 12px;
        }
        @media (min-width: 769px) and (max-width: 1200px){
          padding: 10px 15px;
          font-size: 12px;
        }
      }
    }

    .mobile-area{
      padding: 4px 10px;
      display: flex;
      justify-content: center;
      align-items: center;

      @media screen{
        @media (min-width: 568px){
          display: none;
        }
      }
    }

    .footer{
      padding: 15px 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media screen{
        @media(max-width: 768px){
          padding: 8px 8px;
          font-size: 12px;
        }
        @media (min-width: 769px) and (max-width: 1200px){
          padding: 5px 15px;
          font-size: 12px;
        }
      }

      .center{
        @media screen{
          @media (max-width: 568px){
            display: none;
          }
        }
      }

      .skip-btn{
        padding: 5px 15px;
        @media screen{
          @media (max-width: 768px){
            padding: 1px 8px;
          }
          @media (min-width: 769px) and (max-width: 1200px){
            padding: 3px 10px;
          }
        }
        border-radius: 5px;
        background: @color-white;
        color: @color-skyblue;
        cursor: pointer;
        border: @color-skyblue solid 1px;
        position: relative;
        overflow: hidden;
        z-index: 0;
        i{
          padding: 5px;
        }

        &:hover{
          color: @color-white;
          border: @color-white solid 1px;
        }
        &:hover:before{
          width: 100%;
        }
      }
      .to-left:before{
        content: '';
        background: @color-skyblue;
        position: absolute;
        z-index: -1;
        top: 0;
        bottom: auto;
        left: 0;
        right: auto;
        width: 0;
        height: 100%;
        opacity: 1;
        transition: all 0.3s ease-in-out;
      }
      .to-right:before{
        content: '';
        background: @color-skyblue;
        position: absolute;
        z-index: -1;
        top: 0;
        bottom: auto;
        left: auto;
        right: 0;
        width: 0;
        height: 100%;
        opacity: 1;
        transition: all 0.3s ease-in-out;
      }
    }

    .fill-btn{
      padding: 8px 20px;
      @media screen{
        @media(max-width: 568px){
          padding: 4px 12px;
        }
        @media (min-width: 569px) and (max-width: 768px){
          padding: 3px 8px;
        }
        @media (min-width: 769px) and (max-width: 1200px){
          padding: 5px 12px;
        }
      }
      border-radius: 5px;
      color: @color-white;
      cursor: pointer;
      border: @color-white solid 1px;
      i{
        padding: 5px;
      }
      margin: 5px;
    }
    .pink{
      background: @color-pink;
    }
    .doulv{
      background: @color-doulv;
    }
    .fill-btn.buzz{
      &:hover{
        i{
          animation: icon-rotate 0.7s infinite;
          animation-fill-mode: forwards;
          animation-timing-function: steps(36,end);
        }
      }
    }

    .blog-toc{
      margin-left: 15px;
      width: 100%;
      height: auto;
      border-radius: 10px;
      background: @color-white;
      display: flex;
      flex-direction: column;
      align-items: center;

      .title{
        width: 100%;
        padding: 10px 20px;
        border-bottom: @color-border solid 1px;
        display: flex;
        align-items: center;
        justify-content: start;
        font-size: 16px;
        i{
          margin-right: 5px;
        }
        span{
          font-weight: 600;
        }
      }
    }
  }


</style>